import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatButton } from '@angular/material/button';
import { MatTableDataSource } from '@angular/material/table'
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { IMovimiento } from '../models/movimiento';
import { ITipoMovimiento } from '../../tipo-movimientos/models/tipo-movimiento';
import { IFondo } from '../../fondos/models/fondo';
import { MovimientosService } from '../service/movimientos.service';
import { debounceTime, distinctUntilChanged, firstValueFrom } from 'rxjs';
import { SaveComponent } from './save/save.component';
@Component({
    selector: 'app-movimientos',
    templateUrl: './movimientos.component.html',
    styleUrls: ['./movimientos.component.scss']
})
export class MovimientosComponent implements OnInit {
    public dataSource: MatTableDataSource<IMovimiento> = new MatTableDataSource();

    movimiento: IMovimiento[];
    tipos_movimientos: ITipoMovimiento[] = [];
    fondos: IFondo[] = [];

    columns = ['fecha', 'tipo', 'origen', 'destino', 'importe', 'descripcion', 'acciones']

    form: FormGroup;

    //campo del buscador
    search_descripcion: FormControl = new FormControl('');

    @ViewChild(MatPaginator) paginator!: MatPaginator;
    // MatPaginator atributos
    totalRows = 0;
    pageSize = 25;
    currentPage = 0;
    pageSizeOptions: number[] = [5, 10, 25, 50, 100];
    // MatPaginator Output
    pageEvent: PageEvent;

    //variable auxiliar para el orden de listado
    sortValue: string = 'desc';

    constructor(
        private movimientosService: MovimientosService,
        private menu: JedsMenuService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
    ) { }

    ngOnInit(): void {
        this.getMovimientos();
        this.newForm();
        this.getFondos();
        this.getTiposMovimientos();
        this._listenCambios();
    }

    _listenCambios() {
        // funcion que escucha los cambios del buscador para filtrar
        this.search_descripcion.valueChanges
            .pipe(
                debounceTime(300),
                distinctUntilChanged(),
            )
            .subscribe(() => {
                this._filterMovimientos()
            });

        // funcion que escucha los cambios de los selectores para filtrar
        this.form.valueChanges
            .pipe(
                debounceTime(300),
                distinctUntilChanged(),
            )
            .subscribe(() => {
                this._filterMovimientos()
            });
    }

    /**
     * Formulario de filtros
     */
    newForm() {
        this.form = new FormGroup({
            filtroFecha: new FormControl(('')),
            filtroBonificado: new FormControl(('')),
            filtroTipoMovimiento: new FormControl(('')),
            filtroOrigen: new FormControl(('')),
            filtroDestino: new FormControl(('')),
        });
    }

    /**
     * Esta funcion es para listas los fondos de origen y destino en el filtro de grilla.
     * @returns fondos de origen y destino
     */
    getFondos() {
        const fondos = this.movimientosService.getFondos().subscribe(fondos => { this.fondos = fondos; })
        return fondos;
    }

    /**
     * Esta funcion es para listar los tipos de movimientos en el filtro de la grilla
     * @returns tipos de movimientos
     */
    getTiposMovimientos() {
        const tipos_movimientos = this.movimientosService.getTiposMovimientos().subscribe(tipos_movimientos => { this.tipos_movimientos = tipos_movimientos; })
        return tipos_movimientos;
    }

    /**
     *  -Funcion en el componente.-
     * - Funcion que comunica el componente con los servicios.
     * - Espera que se consulten los movimientos del backend para almacenarlos en una variable.
     * - Teniendo la respuesta del backend en una variable, llama a la funcion 'getDataMovimientos' pasandole por parametro los movimientos.
     * - Le envia el filtro y el paginado al servicio.
     * @param filter Recibe los valores que haya tomado el filtro en la grilla.
     */
    async getMovimientos(filter?: any) {
        const paginado = { pagination: 1, perpage: this.pageSize, currentPage: this.currentPage + 1 };
        const movimientos = await firstValueFrom(this.movimientosService.getMovimientos({ paginado, filter }));
        this.getDataMovimientos(movimientos);
    }

    /**
     *  -Funcion en el componente-
     * - Esta funcion toma los datos que se la pasan por parametro para crear una tabla con AngularMaterial.
     * @param data En data se almacenan los fondos.
     */
    async getDataMovimientos(data) {
        new Promise(async (resolve) => {
            this.movimiento = data.list;
            if (!!this.paginator) {
                this.paginator.pageIndex = this.currentPage;
                this.paginator.length = data['totalCount'];
            }
            this.dataSource = new MatTableDataSource(this.movimiento);
            resolve(true);
        });
    }


    /**
     * -Funcion en el componente-
     * * Setea la nueva cantidad de items que se quiere mostrar por pagina a las variables del componente y asi actualizar la pagina con la nueva cantidad.
     * @param event Es la modificacion en el template de cuantos items se quiere mostrar por pagina.
     */
    actualizarPagina(event: PageEvent) {
        this.pageSize = event.pageSize;
        this.currentPage = event.pageIndex;
        this.getMovimientos();
    }

    /**
     * Funcion en el componente
     * Esta funcion toma los valores que tenga el formulario de filtro y
     * los encapsula en una constante para pasarla como parametro al obtener los movimientos. 
     * @const payload 
     */
    _filterMovimientos($event?: any) {
        if ($event) { this.sortValue = $event.direction }

        const payload = {
            descripcion: (this.search_descripcion.value).toLowerCase(),
            bonificado: this.form.get('filtroBonificado').value,
            tipo_mov: this.form.get('filtroTipoMovimiento').value,
            origen: this.form.get('filtroOrigen').value,
            destino: this.form.get('filtroDestino').value,
            sort: this.sortValue
        }
        this.getMovimientos(payload);
    }

    /**
     * Funcion en el componente
     * - Esta funcion guarda un movimiento.
     * @param documentRef 
     */
    saveMovimiento(documentRef: MatButton) {
        this.menu
            .open(SaveComponent, documentRef._elementRef, {})
            .afterClosed()
            .subscribe({
                next: response => {
                    if (response) {
                        this.getMovimientos();
                    }
                },
            });
    }

    /**
     * Funcion en el componente
     * - Esta funcion actualiza un movimiento.
     * @param documentRef 
     * @param movimiento 
     */
    updateMovimiento(documentRef: MatButton, movimiento: IMovimiento) {
        this.menu
            .open(SaveComponent, documentRef._elementRef, movimiento)
            .afterClosed()
            .subscribe({
                next: response => {
                    if (response) {
                        this._filterMovimientos()
                    }
                },
            });
    }

    /**
     * Funcion en el componente
     * - Esta funcion elimina un movimiento.
     * @param documentRef 
     * @param movimiento 
     */
    deleteMovimiento(movimiento: IMovimiento) {
        this.confirm.open({
            title: "Eliminar movimiento " + movimiento.descripcion,
            message: "¿Desea eliminar el movimiento: " + movimiento.descripcion + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this.movimientosService.deleteMovimiento(movimiento.idmovimiento).subscribe({
                        next: (data) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Movimiento eliminado con éxito.");
                            if (res) {
                                this._filterMovimientos()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.error("Este movimiento no puede eliminarse. Esta asociado a algo más.");
                        }
                    });
                }
            });
    }
}