import { Component, Inject, OnInit } from '@angular/core';
import { IMovimiento } from '../../models/movimiento';
import { MovimientosService } from '../../service/movimientos.service';
import { FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { ITipoMovimiento } from 'app/modules/fondos/tipo-movimientos/models/tipo-movimiento';
import { IFondo } from 'app/modules/fondos/fondos/models/fondo';
import { MAT_DATE_FORMATS } from '@angular/material/core';

/**
 * Formateo de fecha
 */
export const MY_DATE_FORMATS = {
    parse: {
        dateInput: 'LL',
    },
    display: {
        dateInput: 'dd/MM/yyyy',
        monthYearLabel: 'MMM yyyy',
        dateA11yLabel: 'LL',
        monthYearA11yLabel: 'MMMM yyyy',
    },
};

@Component({
    selector: 'app-save-movimiento',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [
        { provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS },
    ]
})
export class SaveComponent implements OnInit {
    columns = ['fecha', 'tipo', 'origen', 'destino', 'importe', 'descripcion', 'bonificado']
    form: FormGroup;
    movimiento: IMovimiento;
    title: string;

    fondos: IFondo[] = [];
    tipos_movimientos: ITipoMovimiento[] = [];

    constructor(
        @Inject('MENU_DATA') public data: any,
        private movimientosService: MovimientosService,
        public alert: AppAlertService,
        public menu: JedsMenuService,
        public fb: FormBuilder,
    ) {
        this.movimiento = this.data ? this.data : null;
        this.title = this.movimiento?.idmovimiento ? 'Editar Movimiento' : 'Nuevo Movimiento';
    }

    ngOnInit(): void {
        this.newForm();
        this.getFondos();
        this.getTiposMovimientos();
    }

    newForm() {
        this.form = this.fb.group({
            fecha: new FormControl ((this.movimiento ? this.movimiento.fecha : ''), [Validators.required]),
            bonificado: new FormControl ((this.movimiento ? this.movimiento.bonificado : '')),
            tipo: new FormControl ((this.movimiento ? this.movimiento.idtipo : ''),[Validators.required]),
            origen: new FormControl ((this.movimiento ? this.movimiento.origen : ''),),
            destino: new FormControl ((this.movimiento ? this.movimiento.destino : '')),
            importe: new FormControl ((this.movimiento ? this.movimiento.importe : ''),[Validators.required]),
            descripcion: new FormControl ((this.movimiento ? this.movimiento.descripcion : ''),[Validators.required]),
        });
    }

    /**
     * Esta funcion la utilizamos cuando necesitamos listar los fondos de origen y destino en el formulario
     */
    getFondos() {
        const fondos = this.movimientosService.getFondos().subscribe(fondos => { this.fondos = fondos; })
        return fondos;
    }
    
    /**
     * Esta funcion la utilizamos cuando necesitamos listar los tipos de movimientos en el formulario 
     */
    getTiposMovimientos() {
        const tipos_movimientos = this.movimientosService.getTiposMovimientos().subscribe(tipos_movimientos => { this.tipos_movimientos = tipos_movimientos; })
        return tipos_movimientos;
    }


    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.movimiento = {
                ...this.movimiento,
                fecha: this.form.get('fecha').value,
                bonificado: this.form.get('bonificado').value ? 1 : 0,
                idtipo: this.form.get('tipo').value,
                origen: this.form.get('origen').value,
                destino: this.form.get('destino').value,
                importe: this.form.get('importe').value,
                descripcion: this.form.get('descripcion').value,
            }
            if (this.movimiento.idmovimiento) {
                this.movimientosService.updateMovimiento(this.movimiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.menu.close(data);
                            }
                        }
                    });
            } else {
                this.movimientosService.saveMovimiento(this.movimiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.menu.close(data);
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.menu.close(false)
    }
}