import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IMovimiento } from '../models/movimiento';
import { ITipoMovimiento } from '../../tipo-movimientos/models/tipo-movimiento';
import { IFondo } from '../../fondos/models/fondo';

@Injectable()
export class MovimientosService {
    constructor(private http: HttpClient) { }

    /**
     * Funcion en el servicio
     * - Esta funcion retorna los movimientos.
     * - Configura los filtros para el paginado. 
     * @param filter 
     * @returns movimientos
     */
    getMovimientos(filter?: any): Observable<IMovimiento[]> {
        //Se aplican filtro, o no, para el paginado.
        let f = '?';
        f += filter?.filter?.sort == 'asc' ? 'sort=idmovimiento' : 'sort=-idmovimiento';
        f += filter?.paginado?.pagination ? '&pagination=1&' : '';
        f += filter?.paginado?.perpage ? 'per-page=' + filter.paginado.perpage + '&' : '';
        f += filter?.paginado?.currentPage ? 'page=' + filter.paginado.currentPage + '&' : '';
        f += filter?.filter?.descripcion? `&filter[descripcion][like]=${filter.filter.descripcion}` : '';
        f += filter?.filter?.bonificado? `&filter[bonificado]=${filter.filter.bonificado}` : '';
        f += filter?.filter?.tipo_mov? `&filter[idtipo]=${filter.filter.tipo_mov}` : '';
        f += filter?.filter?.origen? `&filter[origen]=${filter.filter.origen}` : '';
        f += filter?.filter?.destino? `&filter[destino]=${filter.filter.destino}` : '';
        
        var subject = new Subject<any>();

        this.http.get<IMovimiento[]>(`${environment.fondos.view_movimientos}${f}`, { observe: 'response' })
            .subscribe((resp: any) => {
                subject.next({
                    'list': resp.body,
                    'totalPages': resp.headers.get('x-pagination-page-count'),
                    'totalCount': resp.headers.get('x-pagination-total-count')
                })
            });
        return subject.asObservable();
    }


    /**
     * Funcion en el Servicio 
     * @param movimiento 
     * @returns Un movimiento nuevo nuevo
     */
    saveMovimiento(movimiento: IMovimiento): Observable<IMovimiento> {
        return this.http.post<IMovimiento>(`${environment.fondos.movimientos}`, movimiento);
    }

    /**
     * Funcion en el servicio
     * @param movimiento
     * @returns movimiento actualizado
     */
    updateMovimiento(movimiento: IMovimiento): Observable<IMovimiento> {
        return this.http.put<IMovimiento>(`${environment.fondos.movimientos}` + '/' + movimiento.idmovimiento, movimiento);
    }

    /**
     * Funcion en el servicio
     * @param idmovimiento 
     * @returns Elimina el movimiento
    */
    deleteMovimiento(idmovimiento: number): Observable<IMovimiento> {
        return this.http.delete<IMovimiento>(environment.fondos.movimientos + '/' + idmovimiento);
    }

    /**
    * Funcion en el servicio
    * - Esta funcion retorna los Tipos de movimientos.
    * @returns Tipos de movimientos
    */
    getTiposMovimientos(): Observable<ITipoMovimiento[]> {
        return this.http.get<ITipoMovimiento[]>(`${environment.fondos.fondos_tipo}`);
    }

    getFondos(): Observable<IFondo[]> {
        return this.http.get<IFondo[]>(`${environment.fondos.fondos}`);
    }
}